import React from 'react';
import styled from 'styled-components';
import { Body2, Headline2, Headline3, Footnote2 as DetailTitle, Footnote1 } from '@sberdevices/plasma-styles';
import { buttonSecondary, primary, secondary, white, accent } from '@sberdevices/plasma-tokens';
import { Button, ButtonViews, ButtonSize } from 'components/Button/Button';
import { icon } from 'style/const/icon';
import { noop } from 'tools/noop';
import { transition } from 'style/const/transition';
import { focusBorderMixin } from 'style/mixin/focus';
import { ReactComponent as AddSvg } from 'assets/icons/add.svg';
import { ReactComponent as RemoveSvg } from 'assets/icons/remove.svg';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addItemAction } from 'store/actions/native';
import { formatPrice } from 'store/reducers/helpers/catalogPage';

const StyledSelector = styled.div`
    box-sizing: border-box;
    width: 244px;

    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const StyledSelectorValue = styled(Body2)`
    color: ${primary};
`;

const StyledPriceContainer = styled.div`
    margin-top: 16px;

    display: flex;
`;

const StyledPrice = styled(Headline3)`
    color: ${accent};
`;

const StyledOldPrice = styled(Headline3)`
    color: ${primary};
    position: relative;
    opacity: 0.28;

    margin-left: 24px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        border-top: 4px solid ${primary};
        left: 2px;
        right: 2px;
    }
`;

const StyledItemInfoContainer = styled.div`
    position: relative;
    left: 112px;

    overflow: scroll;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 694px;
    height: 544px;
`;

interface ImageContainerProps {
    backgroundImage: string;
}

const StyledImageContainer = styled.div<ImageContainerProps>`
    position: absolute;
    top: 48px;
    left: 836px;

    width: 330px;
    height: 330px;
    border-radius: 40px;
    background: ${white};
    background-image: url(${({ backgroundImage }) => backgroundImage});
    background-size: cover;
`;

const StyledTitle = styled(Headline2)`
    margin-top: 40px;
`;

const StyledSubTitle = styled.div`
    display: inline;
    color: ${secondary};
`;

const ButtonContainer = styled.div`
    margin-top: 48px;
    display: flex;
    justify-content: space-between;
`;

const StyledButton = styled(Button)`
    margin-right: 24px;
    width: 362px;
`;

const selectorIconSizePx = icon.s.sizePx;

const StyledSelectorButton = styled.button`
    position: relative;

    height: 80px;
    width: 80px;
    border: none;
    border-radius: 24px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: ${buttonSecondary};

    will-change: background-color, transform, opacity;
    transition: ${transition.default};

    ${focusBorderMixin({ borderRadius: 24 })}

    &[disabled] {
        opacity: 0.3;
    }
`;

const StyledAddIcon = styled(AddSvg)`
    height: ${selectorIconSizePx}px;
    width: ${selectorIconSizePx}px;
`;

const StyledRemoveIcon = styled(RemoveSvg)`
    height: ${selectorIconSizePx}px;
    width: ${selectorIconSizePx}px;
`;

type ButtonProps = React.ButtonHTMLAttributes<HTMLButtonElement>;

const LessButton: React.FC<ButtonProps> = (props) => (
    <StyledSelectorButton type="button" aria-label="less" {...props}>
        <StyledRemoveIcon />
    </StyledSelectorButton>
);

const AddButton: React.FC<ButtonProps> = (props) => (
    <StyledSelectorButton type="button" aria-label="more" {...props}>
        <StyledAddIcon />
    </StyledSelectorButton>
);

const StyledInfoBlock = styled.div`
    margin-top: 48px;
    display: flex;
    flex-direction: column;

    &:last-of-type {
        margin-bottom: 40px;
    }
`;

const StyledDetailsContainer = styled.div`
    display: flex;
    justify-content: space-between;

    width: 498px;
`;

const StyledDetail = styled.div`
    display: flex;
    flex-direction: column;
`;

const DetailSubtitle = styled(Footnote1)`
    margin-top: 2px;
    color: ${secondary};
`;

interface ItemInfoProps {
    title: string;
    content?: string;
}

const ItemInfo: React.FC<ItemInfoProps> = ({ title, content }) => {
    if (content) {
        return (
            <StyledInfoBlock>
                <DetailTitle>{title}</DetailTitle>
                <DetailSubtitle>{content}</DetailSubtitle>
            </StyledInfoBlock>
        );
    }

    return null;
};

interface ItemDetailsProps {
    energy?: string;
    protein?: string;
    fat?: string;
    carbohydrate?: string;
}

const ItemDetails: React.FC<ItemDetailsProps> = ({ energy, fat, protein, carbohydrate }) => {
    if (protein && fat && carbohydrate) {
        return (
            <StyledInfoBlock>
                <StyledDetailsContainer>
                    <StyledDetail>
                        <DetailTitle>{energy}</DetailTitle>
                        <DetailSubtitle>ккал</DetailSubtitle>
                    </StyledDetail>
                    <StyledDetail>
                        <DetailTitle>{protein} г</DetailTitle>
                        <DetailSubtitle>белки</DetailSubtitle>
                    </StyledDetail>
                    <StyledDetail>
                        <DetailTitle>{fat} г</DetailTitle>
                        <DetailSubtitle>жиры</DetailSubtitle>
                    </StyledDetail>
                    <StyledDetail>
                        <DetailTitle>{carbohydrate} г</DetailTitle>
                        <DetailSubtitle>углеводы</DetailSubtitle>
                    </StyledDetail>
                </StyledDetailsContainer>
            </StyledInfoBlock>
        );
    }

    return <ItemInfo title="Количество калорий" content={energy} />;
};

interface ItemPriceProps {
    price: number;
    originalPrice?: number;
}

const ItemPrice: React.FC<ItemPriceProps> = ({ price, originalPrice }) => {
    return (
        <StyledPriceContainer>
            <StyledPrice>{formatPrice(price)} ₽</StyledPrice>
            {originalPrice ? <StyledOldPrice>{formatPrice(originalPrice)} ₽</StyledOldPrice> : null}
        </StyledPriceContainer>
    );
};

export const ItemPortal: React.FC = () => {
    const dispatch = useDispatch();
    const id = useSelector(({ itemDetails }) => itemDetails.id);
    const {
        name,
        description,
        storageConditions,
        manufacturer,
        ingredients,
        energy,
        protein,
        fat,
        carbohydrate,
        price,
        quantityLeft,
        originalPrice,
        imageUrl,
    } = useSelector(({ productsData }) => productsData.itemsMap[id]);
    const [count, setCount] = React.useState(1);

    // Логика добавления больше, чем одного при голосовых командах, а также обработка голоса на - или +
    const handleAddOne = React.useCallback(() => {
        setCount(count + 1);
    }, [setCount, count]);

    const handleRemoveOne = React.useCallback(() => {
        setCount(count - 1);
    }, [setCount, count]);

    const handleAdd = React.useCallback(() => {
        dispatch(
            addItemAction({
                item: id,
                count: count.toString(),
            }),
        );
    }, [dispatch, count, id]);

    return (
        <>
            <GradientFade kind="top" sizePx={40} isVisible>
                <GradientFade kind="bottom" sizePx={40} isVisible>
                    <StyledItemInfoContainer id="itemScroller">
                        <StyledTitle>{name}</StyledTitle>
                        {quantityLeft > 0 ? <ItemPrice price={price} originalPrice={originalPrice} /> : null}
                        <ButtonContainer>
                            <StyledSelector>
                                <LessButton onFocus={noop} onClick={handleRemoveOne} disabled={count === 1} />
                                <StyledSelectorValue>{count}</StyledSelectorValue>
                                <AddButton disabled={false} onFocus={noop} onClick={handleAddOne} />
                            </StyledSelector>
                            <StyledButton
                                view={ButtonViews.primary}
                                size={ButtonSize.s}
                                shouldFocusOnMount
                                onClick={handleAdd}
                            >
                                Добавить <StyledSubTitle>{formatPrice(price)} ₽</StyledSubTitle>
                            </StyledButton>
                        </ButtonContainer>
                        <ItemInfo title="Описание" content={description} />
                        <ItemInfo title="Состав" content={ingredients} />
                        <ItemInfo title="Срок и условия хранения" content={storageConditions} />
                        <ItemInfo title="Производитель" content={manufacturer} />
                        <ItemDetails energy={energy} protein={protein} fat={fat} carbohydrate={carbohydrate} />
                    </StyledItemInfoContainer>
                </GradientFade>
            </GradientFade>
            <StyledImageContainer backgroundImage={imageUrl} />
        </>
    );
};
