import { Slide } from 'pure-react-carousel';
import React from 'react';
import styled from 'styled-components';

import { useActiveCategory } from '../Categories.hooks';
import { CategoriesListItem } from '../ListItem/CategoriesListItem';

interface StyledSlideProps {
    isActive: boolean;
}

const StyledSlide = styled(Slide)<StyledSlideProps>`
    display: flex;
    justify-content: center;
    align-items: center;

    transition: margin 0.8s ease;

    will-change: margin;
`;

interface CategoriesSlideProps {
    index: number;
    itemId: string;
}

export const CategoriesSlide: React.FC<CategoriesSlideProps> = ({ index, itemId }) => {
    const activeCategory = useActiveCategory();

    const isActive = activeCategory?.id === itemId;

    return (
        <StyledSlide index={index} isActive={isActive}>
            <CategoriesListItem id={itemId} />
        </StyledSlide>
    );
};
