import { AppReducer, AppState } from 'store/constants';
import { StoreProductItem, ProductItem, ProductItemDeprecated } from 'store/constants/products';
import { selectProductById, selectProductDiffById } from 'store/selectors';
import { isProductItemDeprecated } from 'typeguards/isProductItemDeprecated';

const normalizeProductItemDeprecated = (productItem: ProductItemDeprecated): StoreProductItem => ({
    name: productItem.name,
    id: productItem.guid,
    price: Number(productItem.price),
    quantityLeft: Number(productItem.quantity_left),
    imageUrl: productItem.images_urls[0] || '',
    description: productItem.description,
    brand: productItem.brand,
    manufacturer: productItem.producer,
    country: productItem.country,
    volume: productItem.package_description.volume || '',
    weight: productItem.package_description.weight || '',
    size: productItem.package_description.size || '',
    ingredients: productItem.properties.join(', '),
    energy: (productItem.structured_description.calories || '').toString(),
    protein: (productItem.structured_description.protein || '').toString(),
    fat: (productItem.structured_description.fats || '').toString(),
    carbohydrate: (productItem.structured_description.carbohydrates || '').toString(),
    storageConditions: productItem.storage_conditions,
});

const normalizeProductItem = (productItem: ProductItem): StoreProductItem => ({
    name: productItem.name,
    id: productItem.id,
    price: Number(productItem.pricePerUnit.value),
    quantityLeft: Number(productItem.quantity),
    imageUrl: productItem.imageUrl[0] || '',
    description: productItem.description,
    brand: productItem.brand,
    manufacturer: productItem.manufacturer,
    country: productItem.country,
    volume: productItem.volume,
    weight: productItem.weight,
    size: productItem.size,
    ingredients: productItem.ingredients,
    energy: productItem.energy,
    protein: productItem.protein,
    fat: productItem.fat,
    carbohydrate: productItem.carbohydrate,
    storageConditions: productItem.storageConditions,
});

export const normalizeProducts = (
    productsMaps: Record<string, ProductItem | ProductItemDeprecated>,
): Record<string, StoreProductItem> =>
    Object.keys(productsMaps).reduce<Record<string, StoreProductItem>>((acc, itemId) => {
        const productItem = productsMaps[itemId];

        acc[itemId] = isProductItemDeprecated(productItem)
            ? normalizeProductItemDeprecated(productItem)
            : normalizeProductItem(productItem);

        return acc;
    }, {});

export const computeProductItemsMapBasedOnChanges = (state: AppState) => {
    return Object.keys(state.productsData.itemsMap).reduce<AppState['productsData']['itemsMap']>((result, itemId) => {
        const productItem = selectProductById(state, itemId);
        const productItemDiff = selectProductDiffById(state, itemId);

        result[itemId] = {
            ...productItem,
            ...productItemDiff,
        };

        return result;
    }, {});
};

export const productsData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'SHOW_ITEMS': {
            state.productsData.itemsMap = {
                ...state.productsData.itemsMap,
                ...normalizeProducts(action.payload.stock),
            };

            break;
        }

        case 'APPLY_ITEMS_CHANGES': {
            state.productsData.itemsMap = computeProductItemsMapBasedOnChanges(state);
            break;
        }

        default:
            break;
    }
};
